<template>
 <!-- Slider main container -->
	<div ref="swiper" class="swiper" style="max-width: 816px;">
		<!-- Additional required wrapper -->
		<div class="swiper-wrapper">
			<!-- Slides -->
			<div class="swiper-slide" v-for="data in news_json.items" :key="data.title">
			<div>{{data.title}}</div>
			<div v-html="data.content"></div>
			</div>
		</div>
		<!-- If we need pagination 
		<div class="swiper-pagination"></div>-->

		<!-- If we need navigation buttons 
		<div class="swiper-button-prev"></div>
		<div class="swiper-button-next"></div> -->

		<!-- If we need scrollbar 
		<div class="swiper-scrollbar"></div> -->
	</div>
</template>
<script>
	// Import Swiper Vue.js components
	import Swiper,  { Autoplay}  from 'swiper';

	// Import Swiper styles
	import 'swiper/swiper-bundle.css';
	
	//import newsJson from './dataJson/news_swiper.json'

	export default {
		name: 'NewsSwiper',
		data(){
			return{
				news_json: []
			}
		},
		mounted() {
			fetch('/dataJson/news_swiper.json').then(res => res.json())
			.then(data => {
				this.news_json=data;
			}).catch(err => console.log(err));
		
			Swiper.use([Autoplay]);
			//console.log(SwiperSlide);
			window.news_swiper =  new Swiper('.swiper', {
				autoplay: {
					delay: 3000,
					//waitForTransition:false,
					disableOnInteraction: false
				},
				loop: true,
				slidesPerView: 3,
				spaceBetween: 30,
				breakpoints: {  
					320: {
						slidesPerView: 2,
						spaceBetween: 10
					},
					// when window width is >= 480px
					480: {
						slidesPerView: 3,
						spaceBetween: 10
					},
					// when window width is >= 640px
					640: {
						slidesPerView: 3,
						spaceBetween: 30
					}
				},

			});
			window.news_swiper.autoplay.start();
		},
	};
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.swiper-backface-hidden .swiper-slide{
		border: 1px solid gray;
		margin-bottom: 10px;
		max-width: 250px;
	}
	@media screen and (max-width: 480px){
		.swiper {
			max-width:360px !important;
		}
		.swiper-slide {
			max-width:173px !important;
		}
	}
</style>